.cartcontrol {
  font-size: 0;
}
.cartcontrol .decrease {
  display: inline-block;
  padding: 0 6px 6px 6px;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .decrease.move-transition {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontrol .decrease.move-transition .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .decrease.move-enter,
.cartcontrol .decrease.move-leave {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartcontrol .decrease.move-enter .inner,
.cartcontrol .decrease.move-leave .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cartCount {
  display: inline-block;
  font-size: 10px;
  color: #93999f;
  line-height: 24px;
  text-align: center;
  width: 12px;
  vertical-align: top;
}
.cartcontrol .cartAdd {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  padding: 0 6px 6px 6px;
  color: #00a0dc;
}


