.pad-18 {
  padding: 18px;
}
.food {
  position: fixed;
  z-index: 30;
  top: 0;
  left: 0;
  width: 100%;
  bottom: 48px;
  background-color: #fff;
}
.food.leftIn-transition {
  -webkit-transition: all 0.7s;
  transition: all 0.7s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.leftIn-enter,
.food.leftIn-leave {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .imgContainer {
  width: 100%;
  height: 0;
  padding-top: 100%;
  position: relative;
}
.food .imgContainer img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.food .imgContainer .back {
  position: absolute;
  top: 10px;
  left: 0;
  font-size: 16px;
  color: #fff;
  padding: 8px;
}
.food .tiltle-message {
  position: relative;
}
.food .tiltle-message .name {
  font-weight: 700;
  font-size: 14px;
  color: #07111b;
  margin-bottom: 8px;
}
.food .tiltle-message .sellNum {
  margin-bottom: 18px;
  font-size: 0;
  color: #93999f;
}
.food .tiltle-message .sellNum span {
  font-size: 10px;
}
.food .tiltle-message .sellNum span:first-child {
  margin-right: 12px;
}
.food .tiltle-message .priceGroup .left {
  font-size: 0;
}
.food .tiltle-message .priceGroup .left .now {
  font-size: 14px;
  color: #f01414;
  font-weight: 700;
  margin-right: 12px;
}
.food .tiltle-message .priceGroup .left .old {
  font-size: 10px;
  color: #93999f;
  font-weight: 700;
  text-decoration: line-through;
}
.food .tiltle-message .priceGroup .left .now span,
.food .tiltle-message .priceGroup .left .old span {
  font-size: 10px;
  font-weight: normal;
}
.food .tiltle-message .priceGroup .cartcontrol-wrapper {
  position: absolute;
  bottom: 6px;
  right: 18px;
}
.food .tiltle-message .priceGroup .addToCart {
  position: absolute;
  display: inline-block;
  background-color: #00a0dc;
  padding: 6px 12px;
  box-sizing: border-box;
  bottom: 12px;
  right: 18px;
  font-size: 10px;
  color: #fff;
  line-height: 12px;
  border-radius: 24px;
  z-index: 10;
}
.food .tiltle-message .priceGroup .addToCart.fade-transition {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  opacity: 1;
}
.food .tiltle-message .priceGroup .addToCart.fade-enter,
.food .tiltle-message .priceGroup .addToCart.fade-leave {
  opacity: 0;
  z-index: -1;
}
.food .food-introduce .title {
  font-weight: 700;
  font-size: 14px;
  color: #07111b;
}
.food .food-introduce .info {
  padding: 6px 8px 0;
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  line-height: 24px;
}
.food .ratings {
  padding: 18px 0 0 0;
  background-color: #fff;
  position: relative;
}
.food .ratings:after {
  position: absolute;
  display: block;
  width: 100%;
  bottom: 0;
  left: 0;
  border-top: 1px solid #e6e7e8;
  content: ' ';
}
.food .ratings .title {
  font-weight: 700;
  font-size: 14px;
  color: #07111b;
  margin-left: 18px;
}
.food .Ratelist {
  margin: 0 18px;
  padding: 16px 0;
  position: relative;
  position: relative;
}
.food .Ratelist:after {
  position: absolute;
  display: block;
  width: 100%;
  bottom: 0;
  left: 0;
  border-top: 1px solid rgba(147,153,159,0.1);
  content: ' ';
}
.food .Ratelist .rate-time {
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  margin-bottom: 6px;
}
.food .Ratelist .rate-content {
  font-size: 0;
}
.food .Ratelist .rate-content i {
  font-size: 12px;
  margin-right: 4px;
  line-height: 24px;
}
.food .Ratelist .rate-content i.icon-thumb_up {
  color: #00a0dc;
}
.food .Ratelist .rate-content i.icon-thumb_down {
  color: #93999f;
}
.food .Ratelist .rate-content .text {
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}
.food .Ratelist .avatar {
  position: absolute;
  top: 16px;
  right: 0;
  font-size: 0;
}
.food .Ratelist .avatar .username {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
  vertical-align: top;
}
.food .Ratelist .avatar img {
  width: 12px;
  height: 12px;
  margin-left: 6px;
  border-radius: 50%;
}


