.foodRate {
  margin: 0 18px 0;
}
.foodRate .ratingType {
  padding: 18px 0;
  font-size: 0;
  position: relative;
}
.foodRate .ratingType:after {
  position: absolute;
  display: block;
  width: 100%;
  bottom: 0;
  left: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.foodRate .ratingType>span {
  display: inline-block;
  font-size: 12px;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555d;
}
.foodRate .ratingType>span .count {
  font-size: 8px;
  margin-left: 4px;
}
.foodRate .ratingType>span.active {
  color: #fff;
}
.foodRate .ratingType>span.positive {
  background-color: rgba(0,160,220,0.2);
}
.foodRate .ratingType>span.positive.active {
  background-color: #00a0dc;
}
.foodRate .ratingType>span.negative {
  background-color: rgba(77,85,93,0.2);
}
.foodRate .ratingType>span.negative.active {
  background-color: #4d555d;
}
.foodRate .switch {
  padding: 12px 0;
  color: #93999f;
}
.foodRate .switch i {
  font-size: 24px;
  vertical-align: middle;
  margin-right: 4px;
}
.foodRate .switch i.on {
  color: #00c850;
}
.foodRate .switch span {
  font-size: 12px;
}


